html,body{
    margin:0;
}

button {
    cursor:pointer;
    background:none;
    padding:0;
    border: none;
    display:inline-flex;
    align-items:center;
    color:inherit;
}

* {
    outline:none !important;
}

a {
    color: $primary;
    font-weight:500;
    text-decoration:none;
}
body {
    background-color:#f5f5f5;
}
h1,h2,h3,h4,h5,h6 {
    font-weight:500;
    margin:0;
}
p {
    margin:0;
}
ul {
    margin:0;
    padding-left:24px;
}